@import url('https://fonts.googleapis.com/css?family=Montserrat:regular,bold,italic&subset=latin,latin-ext');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

*{
    color: black;
    font-family: 'Montserrat';
    list-style: none;
    text-decoration: none;
}

.header{
    justify-content: center;
    align-items: center;
    padding: 40px 0;
}

.inner_head{
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    max-width: 1000px;
    min-width: 470px;
}

.logo h1{
    font-weight: normal;
}

.link{
    font-weight: normal;
    align-items: center;
    justify-content: space-between;
    display: flex;
    text-align: center;
}

.link a{
    justify-content: space-between;
    display: flex;
    padding: 0px 1vw;
}

#login{
    background-color: transparent;
    border: 2px solid #b9b9b9;
    border-radius: 50px;
    width: 70px;
    height: 100%;
    padding: 5px 0px;
}

#register{
    background-color: rgba(0, 0, 0, 0.08);
    border: 2px hidden black;
    border-radius: 50px;
    width: 110px;
    height: 100%;
    padding: 5px 0px;
    color: black;
}

@media(max-width: 800px) {
    .inner_head{
        flex-direction: column;
    }
    .link{
        font-weight: normal;
        align-items: center;
        justify-content: space-between;
        display: flex;
        text-align: center;
        flex-wrap: wrap;
    }
}