@import url('https://fonts.googleapis.com/css?family=Montserrat:regular,bold,italic&subset=latin,latin-ext');
@import url('https://fonts.googleapis.com/css2?family=Poppins:regular,wght@300;400;500;600&display=swap');

.text{
    padding-top: 40px;
    align-items: center;
    justify-content: center;
    font-family: 'Poppins';
    font-weight: 300;
}

.text span{
    color: blue;
}

.content p{
    padding-top: 30px;
    font-size: 18pt;
    max-width: 641px;
    min-width: 450px;
    word-break: keep-all;
}

.content img{
    min-width: 235px;
    min-height: 218px;
    margin-left: 40px;
    width: 20%;
    height: 20%;
}

.content{
    display: flex;
}

.all{
    display: flex;
    flex-direction: column;
}

.action{
    margin-top: 10px;
    position: relative;
    min-width: 300px;
    max-width: 300px;
    justify-content: row;
    display: flex;
}

.action input{
    margin: 0;
    border-radius: 50px;
    height: 60px;
    padding: 15px;
    color: black;
    border: none;
    width: 300px;
}

.action input:focus-visible {
    outline: none;
    border: solid black 2px;
}

.action button{
    float: right;
    cursor: pointer;
    justify-content: flex-end;
    position: absolute;
    z-index: 1;
    align-items: center;
    margin: 0;
    border-radius: 50px;
    height: 60px;
    color: black;
    right: 0;
    border: none;
    width: 80px;
    background-color: black;
}

@media(max-width: 800px) {
    .content img {
        display: none;
    }
    .content{
        min-width: 500px;
    }
    .action{
        margin-top: 20%;
    }
}